.nav-tabs {
    height: 64px;
    white-space: normal;
    padding-top: 10px !important;
}

.nav-tabs-active {
    background: -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(transparent), to(hsla(38, 15%, 56%, 0.4)));
    background: -webkit-linear-gradient(transparent, transparent, hsla(38, 15%, 56%, 0.4));
    background: -o-linear-gradient(transparent, transparent, hsla(38, 15%, 56%, 0.4));
    background: linear-gradient(transparent, transparent, hsla(38, 15%, 56%, 0.4));
}

.logo {
    height: 65%;
    margin-top: 5px;
    margin-left: 5px;
}



